









import Vue from "vue";
import Character from '@/entities/Character';
import {startCase} from "lodash"
export default Vue.extend({
  name: 'CharacterDetails',
	props: {
		character: {
			type: Object as () => Character,
			required: true
		}
	},
	data: function() {
		return {
			startCase
		}
	}
})
